<Transition name="main-upcoming-item" mode="out-in"><div class="main-upcoming-item" v-if="idx === slideView"> <img :src="imageFullUrl + movie.backdrop_path" alt="alt"><div class="main-upcoming-item-content"><div class="container"><h1>{{movie.title}}</h1><p>{{movie.overview}}</p><BtnMore :id="movie.id"></BtnMore></div></div><div class="main-upcoming-item-next" @click="$emit('nextSlide')"><img :src="imageFullUrl + next.backdrop_path" alt="alt"><div class="main-upcoming-item-next-content"> <span class="next">Следующий</span><span class="title">{{next.title}}</span></div><div class="main-upcoming-item-next-line"></div></div></div></Transition>