<div class="main-info" :class="{active: selectedId != null}"> <img v-if="selectedMovie" src="@/assets/img/Close.svg" alt="alt" @click="selectedId = selectedMovie = null"><div class="main-info-block" v-if="selectedMovie"> <img :src="imageFullUrl + selectedMovie.backdrop_path" alt="alt" v-if="selectedMovie.backdrop_path"><img src="@/assets/img/nophoto.png" alt="alt" v-else><div class="main-info-content"><div class="main-info-content-block">   <h2>{{selectedMovie.title || selectedMovie.name}}</h2><p>{{selectedMovie.overview || 'Описание отсутствует...'}}</p><p class="date"><span>{{new Date (selectedMovie.release_date).getFullYear() || new Date (selectedMovie.first_air_date).getFullYear()}},</span><span v-for="genre in selectedMovie.genres" :key="genre.id">{{genre.name}}</span><span v-if="selectedMovie.runtime">{{Math.floor(selectedMovie.runtime / 60) + 'h' + (selectedMovie.runtime - (Math.floor(selectedMovie.runtime / 60) * 60)) + 'm'}}</span><span v-else>{{selectedMovie.number_of_seasons + 'S ' + selectedMovie.number_of_episodes+ 'E'}}</span></p><div class="actors"><Actors :type="page" :id="selectedMovie.id" count="4"></Actors></div><BtnMore :id="selectedMovie.id" :page="page"></BtnMore></div></div></div></div>