<section class="main-media"><router-link to="/series"> <span>Сериалы</span><img src="@/assets/img/arrow.svg" alt="alt"></router-link><Swiper :modules="modules" :space-between="25" :navigation="true" :breakpoints="swiperOptions.breakpoints"><SwiperSlide class="main-media-item" v-for="(tv, idx) in popularTvs" :key="tv.id" @click="getTv(tv, idx)"><img :src="imageUrl + tv.poster_path" alt="alt"></SwiperSlide><SwiperSlide class="main-media-item"><router-link to="/series"><img class="arrow" src="@/assets/img/arrow.svg" alt="alt"><span>Все Сериалы</span></router-link></SwiperSlide></Swiper><InfoBlock :selectedId="selectedId" :selectedMovie="selectedMovie" :index="index" page="/tv/">           </InfoBlock></section>